.join {
    display: flex;
    padding: 0 2rem;
    gap: 10rem;
}

.join-left {
color:white;
font-size: 3rem;
font-weight: bold;
text-transform: uppercase;
position: relative;
}

.join-left > hr {
    position: absolute;
    width: 10.5rem;
    border: 2px solid var(--orange);
    margin: -10px 0;
    border-radius: 20%;
    -webkit-border-radius: 20%;
    -moz-border-radius: 20%;
    -ms-border-radius: 20%;
    -o-border-radius: 20%;
}

.join-left > div {
    display: flex;
    gap: 1rem;
}

.join-right{
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.email-container {
    display: flex;
    gap: 3rem;
    background-color: gray;
    padding: 1rem 2rem;
}

.email-container > input,
.email-container > input::placeholder {
    all: unset;
    color: var(--lightgray);
}

.btn-join {
    background-color: var(--orange);
    color: white;
}

@media screen and (max-width: 768px) { 
    .join {
        flex-direction: column;
        gap: 1rem;
    }

    .join-left {
        font-size: x-large;
        flex-direction: column;
    }

    .join-right {
        padding: 2rem;
    }
}